@import url(https://fonts.googleapis.com/css?family=Arvo&display=swap);
body {
  margin: 0;
  font-family: "Arvo", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.span-color {
  color: #22aef5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
}

.App {
  height: 100vh;
}

#app-title {
  margin-top: 10px;
  margin-bottom: 0px;
}

#calendar {
  width: 400px;
}

/* Buttons */

#save {
  display: block;
  width: 240px;
  height: 60px;
  margin-bottom: -5vh;
}

#reset {
  margin-left: 2rem;
}

h1#clock {
  margin-top: 10px;
  margin-bottom: 10px;
}

h2 {
  display: inline-block;
  margin: 0;
}

div button {
  font-family: "Arvo", serif;
  display: inline-block;
  line-height: 20px;
  margin-left: auto;
  margin-right: auto;
  height: 45px;
  width: 120px;
  padding: 5px;
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 15px;
  background-color: #22aef5;
  border: 4px solid #427198;
  border-radius: 10%;
}

/**** Calendar *****/

#calendar p {
  display: inline-block;
  margin-right: 5px;
  border: 2px solid black;
  padding: 10px;
}

.calendar {
  margin-left: auto;
  margin-right: auto;
}

.calendar-container {
  position: relative;
  margin-top: 10px auto 10px auto;
}

.day.current-day {
  color: #22aef5;
}

#legend-container {
  display: inline-block;
}

.legend-color {
  display: inline-block;
  height: 40px;
  padding: 2.5px;
  font-size: 18px;
}

.legend-color p {
  margin-top: 10px;
}

.legend-color.one {
  background-color: #22aef5;
}

.legend-color.two {
  background-color: #43af35;
}

.legend-color.three {
  background-color: #d1d41f;
}

.legend-color.four {
  background-color: #d48b1f;
}

.excellent {
  background-color: #22aef5;
}

.great {
  background-color: #43af35;
}

.good {
  background-color: #d1d41f;
}

.needs-work {
  background-color: #d48b1f;
}

.App {
  text-align: center;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
  margin-top: -15%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #d5ddef;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #7a5ac7;
}

.App-link {
  color: #22aef5;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


